@import url(https://fonts.googleapis.com/css2?family=Armata&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  /* text-align: center; */
  display: flex;
  flex-direction: column;
}
* {
  font-family: 'Armata', sans-serif;
}

div {
  text-align: center;
}

body{
  background-color: rgb(244, 244, 244);
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  font-size: 5rem;

}
.storeItem {
 display: flex;
 justify-content: space-evenly;
 width: 100%;
 flex-wrap: wrap;

}

.center{
  align-self: center;
}
.main{
  display: flex;
  justify-content: center;
}
.big {
  width: 50%;
}
h1{
  font-size: 3.5rem;
}
.App-link {
  color: #61dafb;
}
table, td, th {
  border: 5px solid black;
  padding: 5px;
}
td{
  background-color: lightblue;
  max-width: 201px;
  overflow: hidden;
}
th{
  background-color: gray;
}
table {
  width: 100%;
  max-width: 90vw;
  border-collapse: collapse;
  font-size: 5rem;
}
tbody{
  width: 100%;
}

button {
  width: 100px;
  align-self: center;
  margin: 5px;
  border-radius: 5px;
  font-size: 24px;
}


input{
  height: 50px;
  font-size: 2rem;
}

.image {
  max-width: 350px;
  max-height: 200px;
}
.smolImage {
  max-width: 350px;
  max-height: 100px;
}
.imgWrapper{
  height: 200px;
}
.smolImgWrapper{
  height: 100px;
}
.item {
  border: rgb(58, 58, 58) 3px solid;
  padding: 10px;
  width: 240px;
  border-radius: 5px;
  margin: 10px;
  min-height: 410px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color:rgb(255, 255, 255);
}
.cartitem {
  border: rgb(58, 58, 58) 3px solid;
  padding: 10px;
  width: 26%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: relative;
  border-radius: 5px;
  margin: 10px;
  background-color:rgb(255, 255, 255);
}
.cartitems {
  width: 100%;
  height: 95%;
  overflow-y:scroll;
  flex-wrap: wrap;
  display: flex;
}

.ostoNapit{
 display: flex;
 align-items: center;
 justify-content: center;
}

.ostoNappi{
  position: relative;
}

input[type="number"]{
  width: 50px;
  height: 30px;
  font-size: smaller;
  margin-bottom: 10;
  border-radius: 5px;
  border: 2px solid;
  border-color: rgb(118, 118, 118);
  background-color: #efefef
}
.cart-plus{
  position: absolute;
  right: 50px;
  cursor: pointer;
}

.cart-plus:active {
  animation: App-logo-spin 2s reverse;
}

.Ostoskori{
  width: 95vw;
  min-height: 95vh;
  height: auto;
  background-color: rgba(86, 90, 94, 0.945);
  position: absolute;
  left: 0; 
  right: 0; 
  top: 0;
  bottom: 0;
  margin:auto;
  display:flex;
  flex-wrap: wrap;
}
.form {
  min-height: 95vh;
  height: auto;
  background-color: rgba(86, 90, 94, 0.945);
  position: absolute;
  left: 0; 
  right: 0; 
  top: 0;
  bottom: 0;
  color: white;
  margin:auto;
}

input[type="text"],input[type="email"]{
  width: 400px;
  margin: 10px;
  border-radius: 5px;
  height: 20px;
  font-size: small;
}
label{
  margin: 100px !important;
}
.close {
  position: absolute;
  right: 25px;
  top: 10px;
  color: red;
  font-size: xx-large;
  font-weight: bolder;
  cursor: pointer;
}
.remove {
  position: absolute;
  color: red;
  top:12px;
  right: 15px;
  font-size: xx-large;
  font-weight: bolder;
  cursor: pointer;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

